import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import Vuex from 'vuex'

Vue.config.productionTip = false

Vue.use(Vuex)

// eslint-disable-next-line no-unused-vars
const store = new Vuex.Store({
  state: {
    valsArray: [['', '', ''], ['', '', ''], ['', '', '']],
    colorBoxes: [['', '', ''], ['', '', ''], ['', '', '']],
    playerIcon1: 'close',
    playerIcon2: 'circle-outline',
    playerName1: 'Player 1',
    playerName2: 'Player 2',
    playerIndc: true,
    isWin: false,
    winnerName: "",

  },
  mutations: {
    clicked (state, { row, col }){
      if (state.valsArray[row][col] === '' && !state.isWin) {
        if (state.playerIndc) {
          Vue.set(state.valsArray[row], col, state.playerIcon1)
        }
        else if (!state.playerIndc) {
          Vue.set(state.valsArray[row], col, state.playerIcon2)
        }
        state.playerIndc = !state.playerIndc;
        for(let i = 0; i < 3; i++){
          if(state.valsArray[i][0] === state.valsArray[i][1] && state.valsArray[i][0] === state.valsArray[i][2] && state.valsArray[i][0] != ''){
            Vue.set(state.colorBoxes[i], 0, 'green')
            Vue.set(state.colorBoxes[i], 1, 'green')
            Vue.set(state.colorBoxes[i], 2, 'green')
            state.isWin = true;
            if (state.playerIndc){
              state.winnerName = state.playerName2;
            }
            else
              state.winnerName = state.playerName1;
          }
          else if(state.valsArray[0][i] === state.valsArray[1][i] && state.valsArray[0][i] === state.valsArray[2][i] && state.valsArray[0][i] != ''){
            Vue.set(state.colorBoxes[0], i, 'green')
            Vue.set(state.colorBoxes[1], i, 'green')
            Vue.set(state.colorBoxes[2], i, 'green')
            state.isWin = true;
            if (state.playerIndc){
              state.winnerName = state.playerName2;
            }
            else
              state.winnerName = state.playerName1;
          }
        }
        if(state.valsArray[0][0] === state.valsArray[1][1] && state.valsArray[0][0] === state.valsArray[2][2] && state.valsArray[0][0] != ''){
          Vue.set(state.colorBoxes[0], 0, 'green')
          Vue.set(state.colorBoxes[1], 1, 'green')
          Vue.set(state.colorBoxes[2], 2, 'green')
          state.isWin = true;
          if (state.playerIndc){
            state.winnerName = state.playerName2;
          }
          else
            state.winnerName = state.playerName1;
        }
        else if(state.valsArray[0][2] === state.valsArray[1][1] && state.valsArray[0][2] === state.valsArray[2][0] && state.valsArray[0][2] != ''){
          Vue.set(state.colorBoxes[0], 2, 'green')
          Vue.set(state.colorBoxes[1], 1, 'green')
          Vue.set(state.colorBoxes[2], 0, 'green')
          state.isWin = true;
          if (state.playerIndc){
            state.winnerName = state.playerName2;
          }
          else
            state.winnerName = state.playerName1;
        }
      }
    },

    setName1 (state, text) {

      if(text != state.playerName2){
        if (text === '' || text === null) {
          state.playerName1 = 'Player 1';
        } else {
          state.playerName1 = text;
        }
      }
    },

    setName2 (state, text) {
      if(text != state.playerName1){
        if (text === '' || text === null) {
          this.state.playerName2 = 'Player 2';
        } else
          this.state.playerName2 = text;
      }
    },

    setIcon1 (state, text) {
      if(text != state.playerIcon2){
        if (text === '' || text === null) {
          this.state.playerIcon1 = 'close';
        } else {
          this.state.playerIcon1 = text;
        }
      }
    },

    setIcon2 (state, text) {
      if(text != state.playerIcon1){
        if (text === '' || text === null) {
          this.state.playerIcon2 = 'circle-outline';
        } else {
          this.state.playerIcon2 = text;
        }
      }
    },

    resetBtn (state){
      state.valsArray = [['', '', ''], ['', '', ''], ['', '', '']];
      state.playerIndc = true;
      state.isWin = false;
      state.winnerName = "";
      state.colorBoxes = [['', '', ''], ['', '', ''], ['', '', '']];
    },
  },
})

new Vue({
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
